import React from 'react';
import { CartItemModel } from 'stores/cart';
import withBem from 'hoc/withBem';

import './CartListItem.scss';

interface CartListItemProps extends CartItemModel, BemProps {
    index: number;
    onMoreClick: () => void;
    onLessClick: () => void;
    onSelect: (index: number) => void;
}

const Component: React.FC<CartListItemProps> = ({
    index,
    onLessClick,
    onMoreClick,
    onSelect,
    product: { imagesUrls, clearName, price, quantityLeft },
    count,
    bem,
}) => {
    const [selected, setFocus] = React.useState(false);
    const hasNoMoreItems = Number(quantityLeft) === count;
    const memoizedOnFocus = React.useCallback(() => {
        onSelect(index - 1); // -1       
        setFocus(true);
    }, [onSelect, index]);

    return (
        <div className={bem()}>
            <div className={bem('index-badge')}>{index}</div>
            <div className={bem('image')} style={{ backgroundImage: `url("${imagesUrls[0]}")` }} />
            <div className={bem('info')}>
                <div className={bem('name')}>{clearName}</div>
                <div className={bem('price')}>{Math.floor(Number(price) / 100)} </div>
            </div>
            <div className={bem.modify({ selected }, 'selector')}>
                <button
                    type="button"
                    aria-label="less"
                    className={bem.modify({ less: !!count, remove: !count }, 'selector-button')}
                    onFocus={memoizedOnFocus}
                    onBlur={() => setFocus(false)}
                    onClick={onLessClick}
                />
                <div className={bem('selector-value')}>{count}</div>
                <button
                    type="button"
                    aria-label="more"
                    disabled={hasNoMoreItems}
                    className={bem.modify({ more: true }, 'selector-button')}
                    onFocus={memoizedOnFocus}
                    onBlur={() => setFocus(false)}
                    onClick={onMoreClick}
                />
            </div>
        </div>
    );
};

export const CartListItem = withBem(Component, 'CartListItem');
