import React from 'react';
import withBem from 'hoc/withBem';
import { useFocusOnMount } from 'hooks';

import './Button.scss';

interface ButtonProps extends BemProps, React.HTMLAttributes<HTMLButtonElement> {
    shouldFocusOnMount?: boolean;
}

const Component: React.FC<ButtonProps> = ({ bem, children, className, shouldFocusOnMount, ...props }) => {
    const mountFocusRef = useFocusOnMount<HTMLButtonElement>();

    return (
        <button
            className={bem.all(bem(), className || '')}
            ref={shouldFocusOnMount ? mountFocusRef : null}
            type="button"
            {...props}
        >
            {children}
        </button>
    );
};

export const Button = withBem(Component, 'Button');
